.container-list {
  display: flex;
  flex-direction: column;
  flex-wrap: wrap;
  width: 100%;
  height: 100%;
  color: $color-text-one;
  h2 {
    margin: 10px auto;
    margin-bottom: 50px;
  }
  h5 {
    margin: 10px;
  }
  .list-menu-value {
    h5 {
      color: $color-text-one;
    }
  }
}
.list-menu-day {
  display: flex;
  flex-wrap: wrap;
  flex-direction: row;
  justify-content: space-around;

  &-title {
    width: 100px;
    height: 100px;
  }
}
